import React, { useState, useEffect } from 'react';
import * as styles from './index.module.css';
import * as otherstyles from '../../styles/index.module.css';

const getRandomColor = () => {
    const neonColors = [
        '#FF00FF', // Magenta
        '#00FFFF', // Cyan
        '#FFFF00', // Yellow
        '#FF4500', // Orange
        '#32CD32', // Lime Green
        '#FF1493', // Deep Pink
        '#1E90FF', // Dodger Blue
        '#FFD700', // Gold
        '#ADFF2F', // Green Yellow
        '#FF69B4'  // Hot Pink
    ];
    return neonColors[Math.floor(Math.random() * neonColors.length)];
};

const CursorWords = ({ texts, colorWordIndexes = [] }) => {
    const [currentText, setCurrentText] = useState('');
    const [currentIndex, setCurrentIndex] = useState(0); // Track which word we're on
    const [isTyping, setIsTyping] = useState(true); // Track typing or backspacing
    const maxLength = Math.max(...texts.map((text) => text.length)) + 1; // Longest word's length + 1 for cursor
    const [colorMap, setColorMap] = useState([]);

    useEffect(() => {
        const typingSpeed = 75; // Milliseconds per character when typing
        const backspacingSpeed = 50; // Milliseconds per character when backspacing
        const pauseAfterTyping = 900; // Pause after typing a word
        const pauseAfterDeleting = 200; // Pause after deleting a word

        let timeoutId;

        if (isTyping) {
            if (currentText.length < texts[currentIndex].length) {
                // Add the next character
                timeoutId = setTimeout(() => {
                    setCurrentText((prev) => {
                        const nextText = texts[currentIndex].slice(0, prev.length + 1);
                        if (colorWordIndexes.includes(currentIndex)) {
                            setColorMap((prevMap) => [
                                ...prevMap,
                                getRandomColor(),
                            ]);
                        }
                        return nextText;
                    });
                }, typingSpeed);
            } else {
                // Pause after completing the word
                timeoutId = setTimeout(() => setIsTyping(false), pauseAfterTyping);
            }
        } else {
            if (currentText.length > 0) {
                // Remove the last character
                timeoutId = setTimeout(() => {
                    setCurrentText((prev) => prev.slice(0, -1));
                    if (colorWordIndexes.includes(currentIndex)) {
                        setColorMap((prevMap) => prevMap.slice(0, -1));
                    }
                }, backspacingSpeed);
            } else {
                // Move to the next word and start typing again
                timeoutId = setTimeout(() => {
                    setIsTyping(true);
                    setCurrentIndex((prev) => (prev + 1) % texts.length); // Loop through words
                    setColorMap([]); // Reset color map for new word
                }, pauseAfterDeleting);
            }
        }

        return () => clearTimeout(timeoutId);
    }, [currentText, isTyping, texts, currentIndex, colorWordIndexes]);

    return (
        <span style={{ width: `${maxLength}ch`, display: 'inline-block' }}>
            {colorWordIndexes.includes(currentIndex)
                ? currentText.split('').map((char, index) => (
                      <span key={index} style={{ color: colorMap[index] || '#000' }}>
                          {char}
                      </span>
                  ))
                : currentText}
            <span className={`${styles.cursor} ${styles.greenBrick}`}></span>
        </span>
    );
};

export default CursorWords;
